import React from "react";
import { Alert } from "antd";
import { HubConnectionBuilder, LogLevel } from "@microsoft/signalr";
import { BaseEditor, CodeEditor } from "components/ui/Editor/Editor";
import { toRelativeUrl } from "components/utils/utils";

export default function LiveLog({ traceId }) {
    const [log, setLog] = React.useState("");

    React.useEffect(() => {
        const connection = new HubConnectionBuilder()
            .withUrl(toRelativeUrl(`/loghub`))
            .withAutomaticReconnect()
            .configureLogging(LogLevel.Debug)
            .build();

        connection.on("sendLog", (message) => {
            setLog(prev => prev + `${message}\r\n`);
        });

        connection
            .start()
            .then(() => connection.send("subscribe", traceId))
            .catch((err) => console.log("signalr error", err));
        return () => { connection.stop() };
    }, [traceId]);


    return (
        <Alert.ErrorBoundary>
            <CodeEditor readonly={true}>
                <BaseEditor value={log} language="psulog" options={{ readOnly: true }} />
            </CodeEditor>
        </Alert.ErrorBoundary>
    );
}
